html {
  font-size: 62.5%; }

body {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  min-height: 100vh;
  background-color: var(--background);
  color: var(--primary);
  font-size: 1rem;
  font-family: sans-serif; }

.wrapper {
  width: 90%;
  max-width: 900px;
  margin: 0 auto; }

.content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%; }
  .content__top {
    display: flex;
    flex-direction: column;
    font-size: 1.5rem;
    padding: 1em; }
    .content__top form {
      display: flex;
      justify-content: center;
      align-items: center;
      gap: .5em; }
    .content__top input {
      padding: 1em;
      border-radius: 8px; }
    .content__top .submit {
      padding: 1em;
      border: 1px solid;
      background: none;
      border-radius: 8px;
      color: var(--primary);
      cursor: pointer; }
  .content__middle {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    width: 100%;
    margin-top: 1em;
    padding: 1em; }
    .content__middle .temperature, .content__middle .weather, .content__middle .humidity {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      font-size: 1.2rem;
      letter-spacing: 1px; }
      .content__middle .temperature p, .content__middle .weather p, .content__middle .humidity p {
        font-size: 2rem;
        letter-spacing: 1px; }
  .content__bottom {
    width: 100%;
    padding: 2em; }
    .content__bottom .map {
      width: 100%;
      border: 1px solid black; }

@media (min-width: 768px) {
  .wrapper {
    width: 80%; }
  .content__top {
    align-items: center;
    font-size: 2rem;
    padding: 2em; }
    .content__top form {
      gap: 1.5em; }
    .content__top input {
      padding: 1em 2em;
      font-size: 2rem; }
    .content__top .submit {
      padding: 1em 2em;
      font-size: 2rem; }
  .content__middle .temperature, .content__middle .weather, .content__middle .humidity {
    font-size: 1.8rem; }
    .content__middle .temperature p, .content__middle .weather p, .content__middle .humidity p {
      font-size: 2.5rem; } }
